/* services (home page) */
$m-services-thumb-left: 160px;
$m-services-thumb-left--lg: 100px;
$m-services-thumb-left--sm: 40px;
$m-services-thumb-indent: 320px;
$m-services-thumb-indent--hd: 260px;
$m-services-thumb-indent--lg: 160px;
$m-services-thumb-indent--sm: 40px;

.m-services-container {
	height: 100%;
	overflow: hidden;
	position: relative;
	@include clear; }

.m-services {
	height: 100%;
	margin-left: auto;
	margin-right: auto;
	position: relative;
	overflow: hidden;
	z-index: 1; }

.m-services-visual {
	float: left;
	width: 50%;
	height: 100%;
	background-color: $bg-color-alt;

	@include media('<tab-lg') {
		position: absolute;
		right: 150%;
		top: 0; } }

.m-services-prev, .m-services-next, .m-services-pagination, .m-services__content {
	user-select: none; }

.m-services__item {
	position: relative; }

.m-services__img {
	position: absolute;
	@include size(100%);
	@include position(0);
	background-color: $bg-color-alt;
	z-index: -1;

	img {
		display: block;
		@include size(100%);
		max-width: none;
		@include object-fit(contain); } }

.m-services-title {
	display: block;
	@include font-alt-bold;
	@include font(48px, 1);
	letter-spacing: 0.05em;
	color: inherit;
	margin-bottom: 60px;
	padding-left: $m-services-thumb-indent;

	@include media('<desk-hd') {
		padding-left: $m-services-thumb-indent--hd;
		margin-bottom: 46px; }

	@include media('<desk-lg') {
		padding-left: $m-services-thumb-indent--lg;
		font-size: 36px;
		margin-bottom: 0.75em; }

	@include media('<desk-sm') {
		font-size: 30px; }

	@include media('<tab-lg') {
		padding-left: 0; }

	@include media('<mob-lg') {
		margin-bottom: 0.5em; } }

.m-services-pagination {
	position: relative;
	z-index: 5;

	@include media('<tab-lg') {
		@include margin-horizontal(-#{$layout-spaces--sm});
		@include clear; }

	@include media('<mob-lg') {
		@include margin-horizontal(-#{$layout-spaces--mob}); } }

.m-services__letter {
	display: block;
	position: absolute;
	top: 50%;
	left: 100%;
	width: 400px;
	height: 400px;
	transform: translate(-50%, -50%);
	//background-color: rgba($bg-color-alt, 0.35)

	svg {
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-top: 50px;
		width: 400px;
		height: 400px;
		transform: translate(-50%, -50%);
		font: 50vmin/1 'minionpro-bold', serif;
		text-transform: uppercase; } }

.m-services-thumbs {
	position: relative;
	float: right;
	@include size(50%, 100%);
	overflow-y: auto;
	overflow-x: hidden;
	z-index: 0;

	@include media('<tab-lg') {
		float: none;
		@include size(auto, auto);
		overflow: visible; } }

.m-services-thumbs-align {
	position: relative;
	//margin-right: -100px
	@include padding-vertical($layout-spaces);
	min-height: 100%;

	&::before {
		@include pseudo();
		left: $m-services-thumb-left;
		top: 0;
		@include size(0, 100%);
		border: solid $border-color;
		border-width: 0 0 0 1px;
		z-index: 5;
		pointer-events: none;

		@include media('<desk-lg') {
			left: $m-services-thumb-left--lg; }

		@include media('<tab-lg') {
			content: none;
			display: none;
			left: $m-services-thumb-left--sm; } } }

.m-services-bullet {
	display: block;
	position: relative;
	padding: 20px 0;
	color: inherit;
	@include font(16px, 24px);
	text-decoration: none;

	@include media('<desk-hd') {
		@include padding-vertical(18px); }

	@include media('<tab-lg') {
		float: left;
		width: 50%;
		padding: 10px $layout-spaces--sm;

		&:nth-child(2n + 3) {
			clear: both; } }

	@include media('<mob-lg') {
		width: 100%;
		float: none;
		padding: 13px $layout-spaces--mob; }

	&::before {
		@include pseudo();
		@include size(100%);
		@include position(0);
		background-color: #fafafa;
		opacity: 0;
		z-index: -1; }

	@include clear;

	&::after {
		@include media('<mob-lg') {
			@include pseudo();
			left: $layout-spaces--mob;
			right: $layout-spaces--mob;
			top: 0;
			height: 0;
			border-top: 1px solid #bfbfbf; } }

	&:first-child {
		&::after {
			border: none; } }

	& > i {
		display: block;
		float: left;
		width: $m-services-thumb-left;
		@include font-alt-italic;
		font-size: 18px;
		text-align: center;
		@include margin-vertical(1px, -1px);

		@include media('<desk-lg') {
			width: $m-services-thumb-left--lg; }

		@include media('<tab-lg') {
			width: $m-services-thumb-left--sm;
			text-align: left; }

		&::after {
			@include pseudo();
			@include position(0, $m-services-thumb-left);
			@include size(0, 100%);
			border-right: 1px solid $border-color;

			@include media('<desk-lg') {
				left: $m-services-thumb-left--lg; }

			@include media('<tab-lg') {
				content: none;
				display: none;
				left: $m-services-thumb-left--lg; } } }

	& > span {
		position: relative;
		display: block;
		padding-left: $m-services-thumb-indent;

		@include media('<desk-hd') {
			padding-left: $m-services-thumb-indent--hd; }

		@include media('<desk-lg') {
			padding-left: $m-services-thumb-indent--lg; }

		@include media('<tab-lg') {
			padding-left: $m-services-thumb-indent--sm; }

		&::before, &::after {
			@include pseudo();
			height: 0;
			border: solid $text-color;
			border-width: 1px 0 0;
			top: 50%;
			left: $m-services-thumb-left;
			opacity: 0;
			transform: scale3d(0, 1, 1);

			transition: {
				property: transform, opacity;
				duration: $animation-speed*2;
				timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); }

			@include media('<desk-lg') {
				left: $m-services-thumb-left--lg; }

			@include media('<tab-lg') {
				content: none;
				display: none;
				left: $m-services-thumb-left--sm; } }

		&::before {
			width: 30px;
			margin-left: -30px;
			transform-origin: right center;

			@include media('<desk-lg') {
				width: 25px;
				margin-left: -25px; } }

		&::after {
			width: 70px;
			transform-origin: left center;

			@include media('<desk-lg') {
				width: 25px; } } }

	.no-touchevents &:hover, .no-touchevents &.m-services-bullet-active {
		&::before {
			opacity: 1; }

		& > span {
			color: $base-color;

			&::before, &::after {
				opacity: 1;
				transform: scale3d(1, 1, 1); } } } }


/* services (home page) end */

/* file download */
