// cart-order
.cart-order {
	$td-width--caption: 400px;
	//============
	.c-table, .c-thead, .c-tbody, .c-tfoot, .c-tr, .c-th, .c-td {
		@include media('<tab-lg') {
			display: block; } }

	.c-table {
		@include media('<tab-lg') {
			@include margin-horizontal(-#{$layout-spaces--sm});
			width: auto; } }

	.c-thead {
		@include media('<tab-lg') {
			display: none; } }

	.c-tr {
		@include media('<tab-lg') {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			@include padding-horizontal(#{$layout-spaces--sm + nth($c-table__img-size, 2) + nth($c-table__td-space--md, 2)}, $layout-spaces--sm); } }

	.c-th {
		@include media('<tab-lg') {
			padding: $c-table__th-space--md; } }

	.c-td {
		@include media('<tab-lg') {
			display: flex;
			align-items: center;
			flex: 1 1;
			font-size: 1.6rem;
			padding: $c-table__td-space--md; } }

	.c-td--caption {
		@include media('<tab-lg') {
			padding-left: 0; } }

	.c-td--del {
		@include media('<tab-lg');
		width: $c-table__del-size;
		min-width: $c-table__del-size;
		max-width: $c-table__del-size;
		padding-right: 0; }

	.c-td--size {
		padding-left: 0; }

	.caption-for-table {
		.img-for-table {
			@include media('<tab-lg') {
				margin-right: nth($c-table__td-space--md, 2); } } }

	//product group head
	.c-tr--head {
		@include media('<tab-lg') {
			background-color: #f7f7f7;
			padding-left: $layout-spaces--sm; }

		.c-td {
			@include media('<tab-lg') {
				display: block;
				background-color: transparent;
				border: none;

				&::before {
					content: attr(data-label);
					display: block;
					@include font-d(13px);
					color: #999; } } }

		.c-td--caption {
			@include media('<tab-lg') {
				display: flex;
				width: auto;
				min-width: $td-width--caption;
				max-width: $td-width--caption;
				border-bottom: none;
				@include font-bold;

				&::before {
					content: none; } } }

		.c-td--model {
			@include media('<tab-lg') {
				display: flex;
				flex: 1 1;
				width: auto;
				min-width: calc(100% - #{$td-width--caption});
				max-width: calc(100% - #{$td-width--caption});
				border-bottom: none;
				@include font-bold;

				&::before {
					content: none; } } }

		.c-td--size {
			@include media('<tab-lg') {
				margin-left: #{nth($c-table__img-size, 2) + nth($c-table__td-space--md, 2)}; } }

		.c-td--price-sum {
			@include media('<tab-lg') {
				margin-right: $c-table__del-size; } }

		.c-td--del {
			@include media('<tab-lg') {
				position: absolute;
				right: $layout-spaces--sm;
				top: 20px;
				padding: 0;
				//+margin-horizontal(-14px)
				@include size($c-table__del-size);
				border: none; }

			@include media('<mob-lg') {
				right: $layout-spaces--mob; } } }

	//product group child
	.c-tr--child {
		.c-td--caption {
			@include media('<tab-lg') {
				display: none; } }

		.c-td--model {
			@include media('<tab-lg') {
				display: none; } } }

	// product removed
	.c-tr--removed {
		@include media('<tab-lg') {
			background-color: #fff7f2;
			padding-left: $layout-spaces--sm; }

		.c-td--caption {
			@include media('<tab-lg') {
				width: auto;
				min-width: $td-width--caption;
				max-width: $td-width--caption; } }

		.c-td--model {
			@include media('<tab-lg') {
				width: auto;
				max-width: 100px;
				min-width: 100px; } } }

	// table footer
	.c-tfoot {
		.c-tr {
			@include media('<tab-lg') {
				background-color: $bg-color-alt;
				padding-left: $layout-spaces--sm; } } } }

