/* order list */
// === title ===
.caption-for-table {
	display: flex;
	align-items: center;

	.order-calc & {
		@include media('<desk-sm', '>=tab-lg') {
			display: block; } } }

// === img ===
.img-for-table {
	display: block;
	@include size(nth($c-table__img-size, 1));
	text-decoration: none;
	color: inherit;
	position: relative;
	background-color: #fff;

	.caption-for-table & {
		margin-right: nth($c-table__td-space, 2);

		@include media('<desk-lg') {
			margin-right: nth($c-table__td-space--md, 2); }

		@include media('<desk-sm') {
			margin-right: nth($c-table__td-space--sm, 2); }

		.order-calc & {
			@include media('<desk-sm', '>=tab-lg') {
				margin-right: 0;
				margin-bottom: $grid-space--sm; } } }

	.no-image & {
		background: url(../../img/no-img-product.png) no-repeat center;
		background-size: contain; }

	//&::after
	//ontent: ""
	//isplay: block
	//include ratio(nth($table__img--size, 1), nth($table__img--size, 2))

	img {
		position: absolute;
		left: 0;
		top: 0;
		@include size(nth($c-table__img-size, 1));
		display: block;
		max-width: none;
		@include object-fit(contain); } }

// === title ===
.title-for-table {
	@include padding-vertical(5px);

