// собщение, если коризна пуста или нет элементов
.order-calc__empty {
	display: none;
	padding: 8px;
	@include font-d(16px);
	text-align: center;
	color: $form-error-color;

	@include media('<mob-lg') {
		font-size: 14px; } }

.order-calc__empty-alert {
	//position: fixed
	//right: $grid-space
	//top: $header-height + $grid-space
	animation: {
		delay: 0.33s;
		duration: 2s; }
	opacity: 0;
	visibility: hidden;
	transform: translateX(50px); }

@keyframes cartEmpty {
	0% {
		opacity: 0;
		visibility: visible;
		transform: translateX(50px); }

	13% {
		opacity: 1;
		transform: translateX(0); }

	87% {
		opacity: 1;
		transform: translateX(0); }

	100% {
		opacity: 0;
		visibility: hidden;
		transform: translateX(50px); } }

// если корзина пуста
.order-calc__hasnt-items {
