/* accordion */
.accordion {
	margin-bottom: $bottom-space--ml;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space; }

	&:last-child {
		margin-bottom: 0; } }

.accordion__item {
	position: relative; }

.accordion__header {
	position: relative;
	z-index: 2;

	.accordion__panel & {
		z-index: 1; }

	// active
	//&.is-open
 }	//ackground-color: $bg-color-alt

.accordion__hand {
	position: relative;
	border: solid $border-color;
	border-width: 0 0 1px;
	padding: nth($accordion-space, 1) nth($accordion-space, 2);
	padding-right: nth($accordion-space, 1)*2+nth($accordion-angle-size, 2);
	cursor: pointer;
	user-select: none;

	.shops &:first-child {
		border-top-width: 1px; }

	.accordion__item:first-child & {
		border-top-width: 1px; }

	@include media('<tab-lg') {
		padding: nth($accordion-space--sm, 1) nth($accordion-space--sm, 2);
		padding-right: nth($accordion-space--sm, 1)*2+nth($accordion-angle-size--sm, 2); }

	@include media('<mob-lg') {
		padding: nth($accordion-space--mob, 1) nth($accordion-space--mob, 2);
		padding-right: nth($accordion-space--mob, 1)*2+nth($accordion-angle-size--sm, 2); } }

.accordion__header__text {
	//+font-alt-bold
	@include font-alt-medium;
	//+font(21px, round-float(27/21, 2))
	@include font(28px, round-float(34/28, 2));

	@include media('<tab-lg') {
		font-size: 24px; }

	@include media('<mob-lg') {
		font-size: 20px; } }

.accordion__header__angle {
	display: block;
	position: absolute;
	right: nth($accordion-space, 2);
	top: 50%;
	@include size(nth($accordion-angle-size, 1), nth($accordion-angle-size, 2));
	border: 2px solid currentColor;
	border-radius: 50%;
	margin-top: -#{round(nth($accordion-angle-size, 2)*0.5)};
	cursor: pointer;
	//backface-visibility: hidden
	-webkit-tap-highlight-color: transparent;
	user-select: none;

	@include media('<tab-lg') {
		right: nth($accordion-space--sm, 2);
		//+size(nth($accordion-angle-size--sm, 1), nth($accordion-angle-size--sm, 2))
 }		//margin-top: -#{round(nth($accordion-angle-size--sm, 2)*0.5)}

	@include media('<mob-lg') {
		right: nth($accordion-space--mob, 2); }

	&::before, &::after {
		@include pseudo();
		left: 50%;
		top: 50%;
		//margin-top: -#{round(nth($accordion-angle-line-size, 1)*0.1)}
		//backface-visibility: hidden
		@include size(nth($accordion-angle-line-size, 1), nth($accordion-angle-line-size, 2));
		border-right: 2px;
		background-color: currentColor;
		will-change: transform, opacity;
		transition: {
			property: transform;
			duration: $animation-speed; } }

	&::before {
		$prop: 45deg;
		transform: rotate($prop);
		transform: rotate($prop) translateZ(0);
		margin-left: -#{round(nth($accordion-angle-line-size, 1)*0.8)}; }

	&::after {
		$prop: -45deg;
		transform: rotate($prop);
		transform: rotate($prop) translateZ(0);
		margin-left: -#{round(nth($accordion-angle-line-size, 1)*0.2)}; } }

// hover, active
.no-touchevents .accordion__hand:hover, .accordion__hand.is-open,
.no-touchevents .order-accordion__hand:hover, .order-accordion__hand.is-open {
	color: $link-color; }

// active
.accordion__header__angle {
	.accordion__hand-js.is-open &, .accordion__hand.active-tab &, &.is-open, .is-open > & {
		&::before {
			$prop: -45deg;
			transform: rotate($prop);
			transform: rotate($prop) translateZ(0); }

		&::after {
			$prop: 45deg;
			transform: rotate($prop);
			transform: rotate($prop) translateZ(0); } } }

// click
.accordion__hand:active {
	.accordion__header__angle {
		transform: scale(0.933); } }

.accordion__header__angle:active {
	transform: scale(0.933); }

.accordion__panel {
	display: none;
 }	//background-color: $bg-color-alt

.accordion__panel__frame {
	position: relative;
	border-bottom: 1px solid $border-color;
	will-change: opacity, transform;
	opacity: 0;
	transition: {
		property: opacity;
		duration: $animation-speed; }

	.is-open > & {
		opacity: 1;
		transition-delay: 0.08s; } }

.accordion__panel__content {
	padding: nth($accordion-space, 1) nth($accordion-space, 2) nth($accordion-space, 1)*2;

	@include media('<tab-lg') {
		padding: nth($accordion-space--sm, 1) nth($accordion-space, 2) nth($accordion-space--sm, 1)*2; }

	@include media('<mob-lg') {
		padding: nth($accordion-space--mob, 1) nth($accordion-space, 2) nth($accordion-space--mob, 1)*2; } }

/* accordion end */

/* order */
.order-container {
	&[id] {
		padding-top: $header-height;
		margin-top: -$header-height;

		@include media('<tab-lg') {
			padding-top: $header-height--lg;
			margin-top: -$header-height--lg; } } }

// order layout
.order {
	margin-bottom: $bottom-space;
	border: 0 solid $border-color;

	@include media('<mob-lg') {
		border-bottom-width: 1px; } }

// order caption
.order__caption,
.order__head {
	position: relative;
	display: flex;

	.order-accordion & {
		padding-right: $order-space-h;

		@include media('<tab-lg') {
			padding-right: $order-space-h--md; }

		@include media('<mob-lg') {
			padding-right: 0; } } }

.order__caption {
	background-color: $bg-color-alt;

	@include media('<tab-lg') {
		display: none; } }

.order__head__th,
.order__caption__th,
.order__labels__th {
	display: inline-block;
	vertical-align: top;
	display: flex;
	align-items: center;
	padding: round($grid-space*0.5) #{$grid-space--md};
	@include font(1.6rem, round-float(24/16, 2));
	text-align: left;

	//+media('<desk-lg')
	//adding: round($grid-space*0.5) $grid-space--sm

	//+media('<desk-md')
	//adding: round($grid-space--sm*1.5) $grid-space--sm

	//+media('<desk-sm')
 }	//include padding-horizontal(5px)

.order__caption__th {
	border: solid white;
	border-width: 0 0 0 2px;
	background-color: $bg-color-alt;
	@include font-bold;
	font-size: 1.3rem;
	line-height: round-float(20/13, 2);
	letter-spacing: 0.1em;
	text-transform: uppercase;

	//+media('<desk-md')
	//etter-spacing: normal

	//+media('<desk-sm')
	//ont-size: 1.2rem

	&:first-child {
		border-left: none;
		flex: 1 1; }

	&:nth-child(2) {
		width: $order-color--width; }

	&:nth-child(3) {
		width: $order-price--width; }

	&:last-child {
		border-right-width: 2px; } }

.order__head {
	min-height: 80px;
	border: solid $border-color;
	border-width: 1px 0 0;

	@include media('<tab-lg') {
		min-height: 60px; }

	@include media('<mob-lg') {
		flex-direction: column; }

	.order__item:first-child & {
		border-top-width: 0;

		@include media('<tab-lg') {
			border-top-width: 1px; } }

	[data-label] {
		&::before {
			content: attr(data-label)':';
			@include font-d();
			color: $text-color-light;
			margin-bottom: 5px;
			display: none;

			@include media('<tab-lg') {
				display: block; } } } }

.order-accordion__hand {
	display: none;
	cursor: pointer;
	border: solid $border-color;
	border-width: 0;
	-webkit-tap-highlight-color: transparent;
	user-select: none;

	.order-accordion & {
		display: block; }

	@include media('<mob-lg') {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		position: relative;
		height: 80px;
		border-top-width: 1px;
		padding-left: nth($c-table__img-size, 1) + $grid-space--sm; }

	.accordion__header__angle {
		right: round($order-space-h/2);
		margin-right: -#{round(nth($accordion-angle-size, 1)/2)};

		@include media('<tab-lg') {
			right: 0;
			margin-right: 0; }

		@include media('<mob-lg') {
			position: relative;
			top: auto;
			right: auto;
			margin: 0 10px 0 0; }

		.no-touchevents &:hover, &.is-open {
			color: $link-color; } }

	span {
		display: none;
		@include font(1.6rem, round-float(24/16, 2));

		@include media('<mob-lg') {
			display: block;

			& + span {
				display: none; } }

		.is-open & {
			@include media('<mob-lg') {
				display: none;

				& + span {
					display: block; } } } } }

.order__head__th {
	@include media('<tab-lg') {
		display: block; }

	@include media('<mob-lg') {
		padding: 0 0 20px; }

	&:first-child {
		border-left: none;
		flex: 1 1;
		padding-left: 0;

		@include media('<tab-lg') {
			display: flex; } }

	&:nth-child(2) {
		width: $order-color--width;

		@include media('<tab-lg') {
			width: $order-color--width--sm; }

		@include media('<mob-lg') {
			width: auto;
			padding-left: nth($c-table__img-size, 1) + $grid-space--sm; } }

	&:nth-child(3) {
		width: $order-price--width;
		white-space: nowrap;

		@include media('<tab-lg') {
			width: $order-color--width--sm; }

		@include media('<mob-lg') {
			width: auto;
			padding-left: nth($c-table__img-size, 1) + $grid-space--sm; } }

	&.caption-for-table {
		@include padding-vertical(10px); }

	.color-indicator {
		.color-name {
			display: inline-block; } } }

.order__group {
	position: relative;

	.order-accordion & {
		display: none;

		.no-js & {
			display: block; } } }

.order__group__holder {
	padding-bottom: 28px;

	@include media('<tab-lg') {
		padding-bottom: 0; }

	.order-accordion & {
		@include padding-horizontal($order-space-h);
		position: relative;
		will-change: opacity, transform;
		opacity: 0;
		transition: {
			property: opacity;
			duration: $animation-speed; }

		@include media('<desk-md') {
			@include padding-horizontal(0); } }

	.order-accordion .is-open > & {
			opacity: 1;
			transition-delay: 0.08s; } }

.order__labels {
	display: flex;
	align-items: center;
	border: solid $border-color;
	border-width: 1px 0;
	min-height: 80px;

	@include media('<tab-lg') {
		min-height: 60px; } }

.order__labels__th {
	@include font-bold;
	display: flex;
	font-size: 1.3rem;
	line-height: round-float(18/13, 2);
	letter-spacing: 0.1em;
	text-transform: uppercase;

	&:nth-child(1) {
		width: $order-list-side--width;
		padding-left: 0;

		@include media('<mob-lg') {
			width: nth($c-table__img-size, 1) + $grid-space--sm; } }

	&:nth-child(2) {
		padding-left: 0;

		@include media('<tab-lg') {
			flex: 1 1;
			justify-content: center;
			text-align: center; } }

	[data-short] {
		&::before {
			font-size: 1.3rem;
			line-height: round-float(18/13, 2); } } }

// order list
.order__list {
	@include clear; }

.order__list__side {
	float: left;
	width: $order-list-side--width;
	color: $text-color-light;
	padding-top: $order-list-th--height;

	@include media('<mob-lg') {
		width: nth($c-table__img-size, 1) + $grid-space--sm; }

	& > div {
		padding-bottom: 28px;
		line-height: 31px; } }

.order__list__content {
	position: relative;
	margin-left: $order-list-side--width;

	@include media('<mob-lg') {
		margin-left: nth($c-table__img-size, 1) + $grid-space--sm; }

	&::before, &::after {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		width: 25px;
		z-index: 5;
		pointer-events: none;
		-webkit-tap-highlight-color: transparent;
		user-select: none;
		display: none;

		@include media('<desk-md') {
			display: block; } }

	&::before {
		left: 0;
		$color: white;
		@include linear-gradient(to right, $color 0%, rgba($color, 0.75) 50%, rgba($color, 0) 100%); }

	&::after {
		right: 0;
		$color: white;
		@include linear-gradient(to left, $color 0%, rgba($color, 0.75) 50%, rgba($color, 0) 100%); }

	table, tbody, thead, tfoot {
		display: flex;
		flex-direction: column; }

	table {
		border-collapse: collapse;
		border-spacing: 0;
		margin-bottom: 0;

		tr {
			display: flex;
			justify-content: space-between;
			width: 100%;

			&::before, &::after {
				content: "";
				min-width: 20px;
				display: none;

				@include media('<desk-md') {
					display: table-cell;
					display: flex; } } }

		th, td {
			display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;
			padding: 0 15px;
			width: $order-select-w;
			min-width: $order-select-w;
			box-sizing: content-box;

			@include media('<tab-lg') {
				width: $order-select-w--md;
				min-width: $order-select-w--md; }

			&:first-child {
				padding-left: 0; }

			&:last-child {
				padding-right: 0; } }

		th {
			@include font-normal;
			color: $text-color-light;
			height: $order-list-th--height; }

		td {
			padding-bottom: 28px; } } }

.order__list__layout {
	overflow-y: hidden;
	overflow-x: auto; }

.order-select {
	display: inline-block;
	vertical-align: top;
	max-width: $order-select-w;

	@include media('<tab-lg') {
		max-width: $order-select-w--md; }

	.ui-spinner {
		@include media('<tab-lg') {
			padding-left: 0;
			padding-right: 0;
			height: auto !important; } }

	a.ui-spinner-button {
		@include media('<tab-lg') {
			display: none; } }

	.ui-spinner-input, .ui-spinner-input[type="text"] {
		@include media('<tab-lg') {
			width: $order-select-w--md; } }

	.stock-tape {
		width: 100%;
		height: 2px;
		position: relative;
		margin-top: -1px;
		background: none;
		box-shadow: none;

		&.stock-empty {
			visibility: hidden;
			opacity: 0; } } }

// order result
.order-result {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	line-height: 0;
	background-color: $bg-color-alt;

	@include media('<mob-lg') {
		padding-top: $grid-space--sm;
		background-color: transparent;
		display: block; }

	.order__group & {
		background-color: transparent; }

	.note {
		display: inline-block;
		vertical-align: top;
		text-transform: none;
		font-size: 16px;
		line-height: 20px;
		padding-right: $grid-space;

		@include media('<mob-lg') {
			display: block;
			padding: 0;
			margin-bottom: $grid-space--sm;
			text-align: right; } }

	[class*="btn-"] {
		@include media('<mob-lg') {
			display: block;
			width: 100%; } } }

.order-result__group {
	display: inline-block;
	vertical-align: top;
	display: flex;
	align-items: center;

	@include media('<mob-lg') {
		justify-content: space-between; }

	@include media('<mob-lg') {
		display: block; } }

.order-result__total, .order-result__label {
	@include display-inline-block-middle;
	@include font-d($line-height: 20px);
	padding: 5px $grid-space;

	@include media('<desk-lg') {
		@include padding-horizontal($grid-space--md); }

	@include media('<mob-lg') {
		display: block;
		text-align: right;
		padding: 0;
		margin-bottom: $grid-space--sm; }

	[data-short] {
		@include media('<mob-lg') {
			letter-spacing: normal;

			&::before {
				@include font-d($line-height: 20px); } } } }

.order-result__label {
	@include media('<mob-lg') {
		font-size: 0;
		line-height: 0; }

	&::before {
		@include font-d($line-height: 20px); } }

/* order end */

/* js-loading */
.loader-cover {
	@include size(100%);
	position: absolute;
	left: 0;
	top: 0;
	z-index: 15;
	background-color: rgba(255, 255, 255, 0.50);
	opacity: 0;
	visibility: hidden;
	transition: {
		property: opacity, visibility;
		duration: $animation-speed; }

	@include media('<tab-lg') {
		position: fixed;
		z-index: 10100; }

	.js-loading & {
		opacity: 1;
		visibility: visible; }

	&::before {
		$loader-size: 120px;
		// ==================
		content: "";
		display: block;
		position: fixed;
		left: 50%;
		top: 50%;
		@include size($loader-size);
		transform: translate(-50%, -50%);
		background-image: url(../../img/preloader.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain; } }

